import { MemoryRouter as Router, Routes, Route } from 'react-router-dom';
import MainComponent from './components/MainComponent';

import icon from '../../assets/icon.svg';
import './App.css';
import { WebRTCAdaptor } from './js/webrtc_adaptor';
import { useRef } from 'react';
import { useState } from 'react';

const STATUS_PREPARING = 0;
const STATUS_READY = 1;
const STATUS_STARTED = 1;

var mediaConstraints = {
  video: {
      aspectRatio:16/9,
      frameRate: 20,
  },
  audio: true,
};

function Hello() {

  const [status, setStatus] = useState(0);
  const [hostId, setHostId] = useState(0);


  function generateHostId(){
    return Math.floor(100000 + Math.random() * 900000);
  }

  function initWebRTCAdaptor(publishImmediately: boolean) {
    console.log("init webrtc adaptor")
     var webRTCAdaptor = new WebRTCAdaptor({
      websocket_url: "ws://localhost:5080/WebRTCAppEE/websocket",
      mediaConstraints: mediaConstraints,
      peerconnection_config: {
          'iceServers': [{'urls': 'stun:stun1.l.google.com:19302'}]
      },
      publishMode: "screen",

      sdp_constraints: {
          OfferToReceiveAudio : false,
          OfferToReceiveVideo : false,
      },
      localVideoId: "id-of-video-element", // <video id="id-of-video-element" autoplay muted></video>
      dataChannelEnabled: true,
      callback: (info: string, obj: any) => {
        if (info == "initialized") {
            console.log("initialized");
            setHostId(generateHostId())
            console.log("set status to ready!")
            setStatus(STATUS_READY)
           // webRTCAdaptor.publish("qwe")



        }
  },
  callbackError: function (error: any, message: any) {
    //some possible errors, NotFoundError, SecurityError,PermissionDeniedError

  }
  });

  //webRTCAdaptor.publish("qwe")
   // window.webRTCAdaptor = webRTCAdaptor;
  }



  //window.electron.ipcRenderer.sendMessage('ipc-example', ['from react hello']);
  initWebRTCAdaptor(false)
  let statusText;
  if (status == STATUS_PREPARING) {
    statusText = <span>Preparing</span>
  } else if(status == STATUS_READY) {
    statusText = <span>Ready</span>
  }

  return (
    <div>
      <div className="Hello">
        <img width="200" alt="icon" src={icon} />
      </div>
      <h1>TunaDesk</h1>
      {statusText}
      <span>Host ID: {hostId}</span>

      <h1>TunaDesk</h1>

      <div className="Hello">
        <a
          href="https://electron-react-boilerplate.js.org/"
          target="_blank"
          rel="noreferrer"
        >
          <button type="button">
            <span role="img" aria-label="books">
              📚
            </span>
            Read our docs
          </button>
        </a>
        <a
          href="https://github.com/sponsors/electron-react-boilerplate"
          target="_blank"
          rel="noreferrer"
        >
          <button type="button">
            <span role="img" aria-label="folded hands">
              🙏
            </span>
            Donate
          </button>
        </a>
      </div>
    </div>
  );
}

// calling IPC exposed from preload script
window.electron.ipcRenderer.once('ipc-example', (arg) => {
  // eslint-disable-next-line no-console
  console.log("DATA FROM ELECTRON CAME HERE!")



});

export default function App() {

  return (
    <Router>
      <Routes>
        <Route path="/" element={<MainComponent />} />
      </Routes>
    </Router>
  );
}
